.App {
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: top;
      justify-content: top;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.content-container {
  width: 90%;
  overflow-y: auto;
  overflow-x: hidden;
  -ms-flex-positive: 2;
      flex-grow: 2;
  padding: 15px;
  border-radius: 4px;
  margin-bottom: 30px;
  background: rgba(255, 255, 255, 0.6);
}

.profile-container {
  width: 100%;
  overflow: auto;
  -ms-flex-positive: 2;
      flex-grow: 2;
  padding: 5px;
  border-radius: 4px;
  background: #fff;
  margin-bottom: 5px;
  overflow-x: hidden;
}

nav {
  height: 50px;
  width: 100%;
  background-color: #fff;
  margin-bottom: 30px;
}

nav img {
  height: 45px;
  margin-left: 15px;
}

.error {
  font-size: 12px;
  color: red;
  display: block;
  margin-bottom: 5px;
}

.selected {
  background-color: #B3B3B3
}

.movieDetails {
  width: 100%;
  height: 300px;
  overflow-y: auto;
}

.movieDetails h3 {
  line-height: 1.4;
}

.movieDetails img {
  height: 275px;
}

.movieDetails span {
  display: inline-block;
}

.movieDetails span:after {
  content:"\000A";
  white-space: pre;
}

.movieDetails-title {
  font-weight: 900;
  display: inline;
  margin-right: 15px;
}

.movieDetails-plot {
  margin-left: 30px;
}

.profile-score {
  background-color: #FC6E51;
  color: #ECEFF1;
  border-radius: 50%;
  padding: 5px 10px;
  margin-left: 15px;
  font-size: 1.1em;
}

.leader-score {
  background-color: #FC6E51;
  color: #ECEFF1;
  border-radius: 50%;
  padding: 5px 10px;
  margin: 0 15px;
  font-size: 1.5em;
  font-weight: 900;
}

.leader-rank {
  background-color: #263238;
  color: #ECEFF1;
  border-radius: 5px;
  padding: 10px 20px;
  margin-left: 15px;
  margin-right: 15px;
  font-size: 2em;
  font-weight: 900;
}

.leader-name {
  font-size: 2em;
  font-weight: 900;
  margin: 0 20px;
}

.mapList{
 -ms-flex-positive: 1;
     flex-grow: 1;
 height: 100vh;
 top: 0;
 position: -webkit-sticky;
 position: sticky;
}
.map{
 display: -ms-flexbox;
 display: flex;
 -ms-flex-wrap: wrap;
     flex-wrap: wrap;
 -ms-flex-line-pack: start;
     align-content: flex-start;
}

.help {
  padding: 15px;
}

.help p {
  margin-left: 15px;
}

h1 {
  color: #00A3E1;
}

.mainHeader {
  color: #00A3E1;
  font-size: 2.5em;
  font-weight: bold;
}

p {
  color: #263238;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}




